* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  color: #212121;
  text-decoration: none;
}

#root, html, body {
  margin: 0;
  padding: 0;
  height: 100vh;
}

body {
  font-family: Arial, Helvetica, sans-serif;
}

label.brand {
  font-size: 2em;
  font-weight: bold;
  line-height: 10vh;
  margin-left: 5%;
}

nav ul {
  float: right;
  margin-right: 5%;
}

nav ul li {
  display: inline-block;
  line-height: 10vh;
  padding:  0 2rem;
}

h1 {
  text-align: center;
}